/*! ===============
End Home 
==================*/

main {
   height: 100vh;
   background-size: cover;
   background-image: url(/imgs/bg.jpg);
   display: flex;
   flex-direction: column;
   position: relative;
   &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($color: #000000, $alpha: 0.4);
      z-index: 0;
   }
   .inner-container {
      margin: auto;
      display: flex;
      justify-content: center;
      text-transform: capitalize;
      position: relative;
      z-index: 1;
      .text {
         background-color: #6b6b713b;
         padding: 30px;
         border-radius: 10px;
         h3 {
            font-size: 1.5rem;
            color: var(--mainColor);
            text-align: center;
         }
         h2 {
         font-weight: 500;
         font-size: 2.7rem;
         letter-spacing: 1px;
         color: #E1E1E1;
         margin-top: 5px;
         }
         span {
            color: var(--mainColor);
         }
         p {
            color: #E1E1E1;
            font-weight: 300;
            font-size: 1.2rem;
            margin: 15px 0;
         }
         .order {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-top: 25px;
            button {
               background-color: var(--mainColor);
               color: #1a191b;
               text-transform: capitalize;
               font-size: 1.1rem;
               font-weight: 700;
               padding: 10px 15px;
               border-radius: 4px;
               transition: var(--transitionDuration);
               &:hover {
                  background-color: #d8ac4c;
               }
            }
            a {
               color: var(--mainColor);
               font-weight: 600;
               font-size: 1.3rem;
               text-decoration: underline;
               transition: var(--transitionDuration);
               &:hover {
                  color: var(--secColor);
               }
            }
      }     
   }
   }
}